/* 공통 */
*{box-sizing: border-box; margin:0; padding:0;}
html, body, div, span, iframe, h1, h2, h3, h4, h5, h6, p, pre, a, img, q, b, u, i, ul, li, fieldset, form, label, legend, table, tbody, tfoot, thead, tr, th, td {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    font-size: 100%;
    vertical-align: baseline;
    background: transparent;
}
body{min-width: 320px; height:100%; font-family:'Nanum Gothic',sans-serif; background:#fff; font-size: 12px; font-weight:300;}
ul, li{list-style: none outside;}
img{width:100%; height:100%;}
a{text-decoration: none; color:black;}

/* 다크모드 */
body{background: white; color:black;}
body.dark{background: black; color: white; border-color: white;}

/* body */
.body-wrapper{display:flex; flex-direction:column; position:relative; min-height:100%; padding: 75px 0 0;}

/* 네비게이션 */
.nav{width:800px; margin:0 auto;}
.navi{width:100%; height: 75px; min-width:320px; display:flex; justify-content:center; position:absolute; top:0; left:0; right:0; border-bottom:1px solid black}
.navList{display: flex; justify-content:center; width:70%; align-items:center;}
.nav-depth1{width:100%; display:flex; font-size: 20px;}
.nav-depth2{width:33%; display:flex; justify-content:center;}

/* 메인 */
.container{display:flex; justify-content:center; min-height:600px;}
.content{width: 80%; max-width:800px; height: 100%; display: flex; flex-direction:column; align-content:center; flex-wrap:wrap; box-shadow: 0px 5px 5px 5px rgba(0, 0, 0, 0.2)}

/* 검색창 */
.search_threads_form{width: 100%; max-width:600px; margin: 10px 0 0 0; position: relative;}
.search_input_text{display: block; height:50px; line-height: 50px; text-align: left; padding-left: 10px; width: 100%;}

/* 메뉴판 */
.menu_list{display:flex; width: 100%;}
.menu_list li{width: 25%; height: 50px; box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.2); font-size: 15px; font-weight: 600;}
.menu_item{display: flex; justify-content: center; align-items: center;}
.board_list_wrapper{padding: 0 10px 0;}

/* 메인 보드 */
.board_list_title{padding: 20px 0 10px; border-bottom: 1px solid black; font-size: 20px; font-weight: 600; display: flex; align-items: center;}
.board_list_title :nth-child(1){width:70%}
.board_list{padding: 10px 0; border-bottom: 1px solid black;}
.link{width:100%; height: 50px;}
.board_box{width: 100%; padding: 0 10px; display: table-cell; vertical-align: top;}
.board_title{font-size: 13px; color: black;}
.board_detail{font-size: 10px; color: black; padding: 10px 0 0px;}
.board_more_link{font-size: 15px; display: flex; align-items: center; justify-content: center;}
/* 메인 끝 */

/* 판 */
.pan_wrapper{width: 100%;}
.pan_board_list_wrapper{display: flex; justify-content: center;}
.pan_board_list{padding: 50px 10px 0; display: flex; flex-direction: column; align-items: center;}
.pan_board_list_title{font-size: 16px; font-weight: 600; padding-bottom: 30px;}
.pan_board_list_item{font-size: 13px;  display: flex; flex-direction: column; align-items: center;}
.pan_board_list_item li{padding-bottom: 20px;}

/* 보드 */
.board_wrapper{width: 100%;}
.make_thread{font-size: 15px;}
/* 페이징 */
.paging-area{display: flex; justify-content: center; align-items: center; font-size: 15px; padding-top: 20px;}
.paging{display:flex;}
.paging div{padding: 0 10px 0;}
/* 보드 끝 */

/* 보드 작성 */
.margin_top{margin-top: 5px;}
.enrill_table{font-size: 15px;}
.enroll_table_wrapper{width: 100%; display: flex; flex-direction: column; align-items: center; margin-top: 50px;}
.enroll_table{width:100%; padding: 10px;}
.enroll_table div{width:100%;}
.enroll_subject_wrapper{display: flex;}
.enroll_subject{font-size: 15px; font-weight: 600;}
.board_title{width:100%;}
.enroll_content_wrapper{width: 100%;}
.board_content{width:100%; height:300px; font-size: 13px;}
.enroll_id{display: flex;}
.writer{width:150px;}
.password{width:150px;}

/* 게시글 상세 */
.detail_table_wrapper{width:100%; border: 1px solid black;}
.detail_table{width: 100%; padding: 10px;}
.detail_subject_detail{font-size: 15px; font-weight: 600;}
.detail_subject_detail span{border-bottom: 3px solid blue;}
.detail_content_wrapper{padding-bottom: 50px; border-bottom: 1px solid black;}
.board_title_detail{font-size:30px; color: blue; font-weight: 600; width: 100%; height: auto; margin-top: 20px;}
.board_content_detail{font-size:15px; width: 100%; height: auto;}
.board_writer{font-size:13px; margin-top:50px;}
/* 댓글 */
.reply_wrapper{margin-top: 50px;}
.reply{margin-top: 20px; font-size: 13px;}
.reply_content{padding: 10px 0 50px; border-bottom: 1px solid black;}


/* 댓글 작성 */
.insert_reply_wrapper{width: 100%; margin-top: 50px;}
.insert_reply_title{font-size: 15px; font-weight: 600;}
.insert_reply_title span{border-bottom: 3px solid blue;}
.insert_reply_writer input{width: 150px; margin-top: 20px;}
.insert_reply_content textarea{width: 100%; height: 200px;}